* {
    box-sizing: border-box;
}

body, html {
    height: 100%;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgb(253, 165, 0);
}

/* HEADER */

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(17, 16, 16);
    color: white;
    min-height: 8vh;
    border-bottom: 1px solid white;
}

.logo {
    letter-spacing: 2px;
    font-size: 25px;
}

.nav-links {
    display: flex;
    justify-content: space-around;
    width: 30%;
}


.nav-icon {
    display: none;
    cursor: pointer;
}

.nav-icon div {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 5px;
    transition: all 0.3s ease;
}

.nav-links li {
    list-style: none;
    font-size: 20px;
}


/* Links */

a {
    text-decoration: none;
    color: white;
}

.nav-links a:focus,
.nav-links a:hover {
    color: rgb(253, 165, 0);
}

.buy-now {
    border: none;
    color: black;
    background-color: white;
    padding: 10px;
    border-radius: 4px;
}

.buy-now:hover,
.buy-now:focus {
    font-weight: bold;
}

.pizza-item a:hover,
.pizza-item a:focus {
    color: rgb(253, 165, 0);
}


/* MAIN */

.main {
    background-color: rgb(17, 16, 16);
    padding: 40px 0;
    margin-top: 0;
    display: flex;
    text-align: center;
    color: white;
}

.left {
    width: 60%;
    margin:0;
    padding: 0;
}

.left h1 {
    font-size: 70px;
    margin-bottom: 0;
}

.left p {
    margin-top: 10px;
    font-size: 20px;
    font-style: italic;
    color: white;
}

.right img {
    width: 400px;
    border-radius: 20px;
    margin-top: 20px;
}



/* ABOUT US */

#about-us {
    background-color: rgb(253, 165, 0);
    color: rgb(17, 16, 16);;
    padding: 10px 10px 50px 10px;
    margin-top: 20px;
    text-align: start;
}

#about-us h1 {
    font-size: 40px;
    text-transform: uppercase;
    text-align: center;
}

#about-us p {
    width: 600px;
    margin: auto;
    line-height: 1.5;
}

/* TOP PIZZA */

.pizza-img {
    width: 150px;
    border-radius: 50%;
}

.top-pizza {
    background-color: rgb(17, 16, 16);
    color: white;
    padding: 10px;
}

.top-pizza h2 {
    font-size: 40px;
    text-transform: uppercase;
    text-align: center;
}

.pizza-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 20px;
}

.pizza-item p {
    font-size: 20px;
    margin: 10px;
}


/* QUOTES */

.quotes {
    text-align: start;
    padding: 40px;
}

.quotes h2 {
    font-size: 40px;
    text-transform: uppercase;
    text-align: center;
}

.quote p {
    width: 600px;
    margin: auto;
    font-size: 20px;
    font-style: italic;
}

.author {
    font-size: 20px;
    text-align: end;
    font-weight: bold;
}

/* MENU */

#menu {
    background-color: rgb(17, 16, 16);
    color: white;
    text-align: start;
    padding: 40px;
}

#menu h2 {
    font-size: 40px;
    text-transform: uppercase;
    text-align: center;
}


.menu-list {
    background-color: rgb(253, 165, 0);
    border-radius: 40px;
    width: 700px;
    padding: 20px;
    text-align: start;
    margin: auto;
}

.menu-item {
    color: black;
    margin-top: 20px;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
    display: flex;
}


/* FOOTER */

#footer {
    background-color: rgb(17, 16, 16);
    padding: 10px 0;
    color: white;
    border-top: 1px solid white;
}

#footer ul {
    list-style: none;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    text-align: center;
}

.copyright {
    text-align: center;
}

#footer li {
    margin: 10px 0;
}

#footer a:hover,
#footer a:focus {
    color: rgb(253, 165, 0);
}


/* Media Query */

/* Extra small devices (phones) */
@media only screen and (max-width: 483px) {
    
    .nav-links {
        display: flex;
        justify-content: space-around;
        width: 60%;
    }

    .logo {
        font-size: 20px;
    }

    .main {
        display: block;
        padding-top: 10px;
    }

    .left {
        width: 100%;
        margin: 0 0 10px 0;
    }
    
    .right img {
        width: 95%;
    }

    #about-us p {
        width: 300px;
    }

    .top-pizza {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        text-align: center;
    }

    .pizza-list {
        display: block;
    }

    .quote p {
        width: 300px;
    }

    .menu-list {
        width: 300px;
    }

    .menu-item {
        display: block;
        text-align: center;
    }


    #footer li {
        margin: 10px auto;
    }

    #footer ul {
        margin: 0;
        padding: 0;
        text-align: center;
    }

    .copyright {
        font-size: 14px;
    }
  }

 



/* Small devices (phones) */
@media only screen and (min-width: 484px) and (max-width: 767px) {
    
    nav {
        display: flex;
        justify-content: space-between;
    }

    .nav-links {
        display: flex;
        justify-content: space-around;
        width: 60%;
    }

    .main {
        display: block;
    }
    .left, .right {
        width: 100%;
    }
    .left {
        margin-bottom: 15px;
    }

    #about-us p {
        width: 300px;
    }

    .top-pizza {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        text-align: center;
    }

    .quote p {
        width: 300px;
    }

    .menu-list {
        width: 400px;
    }

    .menu-item {
        display: block;
        text-align: center;
    }

    #footer li {
        margin: 10px auto;
    }
    
    #footer ul {
        margin: 0;
        padding: 0;
        text-align: center;
    }
  }
  
  @media only screen and (min-width: 768px) and (max-width: 1023px) {

    .right img {
        width: 300px;
    }

    .top-pizza {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        text-align: center;
    
    }

    nav {
        display: flex;
        justify-content: space-between;
    }

    .nav-links {
        display: flex;
        justify-content: space-around;
        width: 60%;
    }

  }

 